//image的过程包装，创建div，添加class,设置高度和宽度
function createAndSet(obj,cla='img_wraper'){
	let image_wraper = document.createElement('div');


	image_wraper.classList.add(cla);

	if(obj){
		for(let str in obj){
			image_wraper.style[str] = obj[str]
		}	
	}
	
	
	return image_wraper;
}

export {createAndSet};


