function imgProp(wraper,srcP,src){
	let img = new Image();

	img.src = src;
	
	wraper.style.backgroundImage = 'url('+srcP+')';
	wraper.classList.add('puzzle-img-wraper');

	img.onload = function(){
		wraper.style.backgroundImage = 'url('+src+')';
	}
	
}
export {imgProp};


